<template>
  <div>

    <a-card style="width: 60%; margin: 0 auto; margin-top: 20px">
      <a-table :data="students" :pagination="false">
        <template #columns>
          <a-table-column title="学号" data-index="user_id"></a-table-column>
          <a-table-column title="该科得分" data-index="score"></a-table-column>
          <a-table-column title="" :width="170">
            <template #cell="{ record }">
              <a-input style="width: 100%" v-model="record.score" type="number" class="score-input"></a-input>
            </template>
          </a-table-column>
        </template>
      </a-table>
      <div class="button-container">
        <a-button @click="submitScores" type="primary">提交</a-button>
      </div>
    </a-card>
  </div>
</template>
  
<script>
import teacher_api from "@/api/teacher/teacher-api"

export default {
  data() {
    return {
      errorMessage: "",
      course_id: "", 
      students: []// 存储学生数据和分数
    };
  },

  mounted() {

    this.course_id = this.$route.query
    
    // console.log(this.course_id)
    teacher_api.getStudentScore(this.course_id).then(resp => {
      this.students = resp.data
      console.log(this.students)
    })

  },


  methods: {

    submitScores() {
      teacher_api.giveScore(this.students).then(resp => {
        if (resp.status_code != 200) {
          if (resp.status_code == 500) {
            this.$message("服务器内部异常")
          }
          else {
            this.$message(resp.status_text + " / " + resp.data)
          }
          return
        }
        this.$message("录入成功")
        this.$router.push({ path: '/teacher/Home'})
      }).catch(err => {
        if (err.code === "ERR_NETWORK") {
          this.$message("网络错误")
        }
        else {
          this.$message(err.message)
        }

      })

    },

  }
};
</script>
 
<style>
.button-container {
  display: flex;
  justify-content: center;
  /* 使子元素在水平方向居中 */
  margin-top: 20px;
  /* 在按钮和表格之间添加一些间隙 */
}
</style>